import React from 'react';
import './Cards.css';
import CardItem from './CardItem';

function Cards() {
    return (
        <div id="projects" className='cards'>
            <h1>Check out these EPIC projects!</h1>
            <div className='cards__container'>
                <div className='cards__wrapper'>
                    <ul className='cards__items'>
                        <CardItem
                            src='images/postit.jpg'
                            text='Keeper App: An app that allows a note to be added and deleted.'
                            label='React.js'
                            path='//jonguolee.github.io/keeper-app/'
                        />
                        <CardItem
                            src='images/notebook.jpg'
                            text='ToDo List: A simple ToDo list constructed with EJS templating with a backend served up by MongoDB.'
                            label='Node/Express.js'
                            path='//obscure-headland-52534.herokuapp.com/'
                        />
                    </ul>
                    <ul className='cards__items'>
                        <CardItem
                            src='images/dog.jpg'
                            text='TinDog Website: A simple landing page showcasing web design and layout principles with Bootstrap.'
                            label='HTML/CSS'
                            path='//jonguolee.github.io/tindog-project/'
                        />
                        <CardItem
                            src='images/dice.jpg'
                            text='Dice Game: Generate two player dice with a random value with a response based on which die has a greater value.'
                            label='Javascript'
                            path='//jonguolee.github.io/dice-game/'
                        />
                        <CardItem
                            src='images/drums.jpg'
                            text='Drum Kit: A simple website that utilizes event listeners to detect and respond to user interaction.'
                            label='Javascript'
                            path='//jonguolee.github.io/drum-kit/'
                        />
                    </ul>
                    <ul className='cards__items'>
                        <CardItem
                            src='images/simon.jpg'
                            text='Simon Game: A game in which a user must follow a random pattern that increases in length and complexity. 
                            WARNING: Sounds in this game are LOUD. Will adjust sound file volume soon.'
                            label='Javascript/jQuery'
                            path='//jonguolee.github.io/simon-game/'
                        />
                        <CardItem
                            src='images/blog.jpg'
                            text='Blog Website: A daily journal that allows posts to be composed and saved on the backend with MongoDB.'
                            label='Node/Express.js'
                            path='//young-bayou-21979.herokuapp.com/'
                        />

                    </ul>
                </div>
            </div>
            <hr />
            <h1>Current and upcoming projects.</h1>
            <div className='cards__container'>
                <div className='cards__wrapper'>
                    <ul className='cards__items'>
                        <CardItem
                            src='images/hmw.png'
                            text='Houston Money Week Website: Re-design launching soon!'
                            label='WordPress'
                            path='//www.houstonmoneyweek.org/'
                        />
                        <CardItem
                            src='images/pioneers.png'
                            text='Pioneers Website: Design work-in-progress.'
                            label='HTML/CSS'
                            path='//jonguolee.github.io/pioneers-project/'
                        />
                        <CardItem
                            src='images/soon.jpg'
                            text='Netflix Clone App: Coming Soon!'
                            label='React.js'
                            path='/'
                        />
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default Cards;

