import React from 'react';
import { Button } from './Button';
import './Contact.css';


function Contact() {
    return (
        <div id="contact" className='contact'>
            <h1>Get In Touch.</h1>
            <div className="cta">
                <p className="outro-text">Have a project you need work on? Let's connect! I am always open for a challenge or opportunity to expand my skills.</p>
                <Button className="btn btn-mobile" onClick={"mailto:jonguolee@gmail.com"} buttonStyle="btn--primary" buttonSize="btn--large" >CONTACT ME</Button>
            </div>
        </div>
    );
}

export default Contact;